import { Component, OnInit } from '@angular/core';
declare var BMap:any;
@Component({
  selector: 'app-localhospital',
  templateUrl: './localhospital.component.html',
  styleUrls: ['./localhospital.component.css']
})
export class LocalhospitalComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  	 // API
	var map = new BMap.Map("allmap");
	var point = new BMap.Point(119.405842,32.377379);

	// 
  var navigationControl = new BMap.NavigationControl({
    // 
   
    enableGeolocation: true
  });
  map.addControl(navigationControl);
  // 
  var geolocationControl = new BMap.GeolocationControl();
  geolocationControl.addEventListener("locationSuccess", function(e){
    // 
    var address = '';
    address += e.addressComponent.province;
    address += e.addressComponent.city;
    address += e.addressComponent.district;
    address += e.addressComponent.street;
    address += e.addressComponent.streetNumber;
    alert("" + address);
  });
  geolocationControl.addEventListener("locationError",function(e){
    // 
    alert(e.message);
  });
  map.addControl(geolocationControl);
	map.centerAndZoom(point, 15);
	map.enableScrollWheelZoom();   //
	map.enableContinuousZoom();    //
	var data_info = [[119.410206,32.374592,""],
					 [119.410092,32.392688,""],
					 [119.423852,32.381673,""],
					 [119.402007,32.402006,""],
					 [119.394809,32.397643,""]
					];
	//logo
	var pt = new BMap.Point(119.405842,32.377379);
	var myIcon = new BMap.Icon("../../assets/maplogo.png", new BMap.Size(140,190));
	var marker2 = new BMap.Marker(pt,{icon:myIcon});  // 
	map.addOverlay(marker2);

	var opts = {
	  position : point,    // 
	  offset   : new BMap.Size(-10, 86),    //

	  width : 250,     // 
	  height: 80,     // 
	  title : "" , // 
	  enableMessage:true//
	}
	var label = new BMap.Label("", opts);  // 
		label.setStyle({
			 color : "black",
			 fontSize : "12px",
			 height : "20px",
			 lineHeight : "20px",
			 fontFamily:"",
			 border:"none"
		 });
	map.addOverlay(label);

	for(var i=0;i<data_info.length;i++){
		var marker = new BMap.Marker(new BMap.Point(data_info[i][0],data_info[i][1]));  // 
		var content = data_info[i][2];
		map.addOverlay(marker);               // 
		addClickHandler(content,marker);
	}
	function addClickHandler(content,marker){
		marker.addEventListener("click",function(e){
			openInfo(content,e)}
		);
	}
	function openInfo(content,e){
		var p = e.target;
		var point = new BMap.Point(p.getPosition().lng, p.getPosition().lat);
		var infoWindow = new BMap.InfoWindow(content,opts);  //  
		map.openInfoWindow(infoWindow,point); //
	}
  }

}



